<template>
    <div class="goodsinfo-container">
        <!-- <transition
            @before-enter="beforeEnter"
            @enter="enter"
            @after-enter="afterEnter">
            <div class="ball" v-show="ballFlag" ref="ball"></div>
        </transition> -->
        <transition name="donghau1" @before-enter="beforeEnter" @enter="enter" @after-enter="afterEnter">
          <div class="ball" v-show="flag" ref="ball"></div>
        </transition>
        <!--  -->
        <swiper-box :lunbolist="lunbolist" :isfull="false"></swiper-box>
        <!--  -->
        <div class="mui-card">
            <div class="mui-card-header">{{goodsinfo.title}}</div>
            <div class="mui-card-content">
                <div class="mui-card-content-inner">
                    <p class="price">
                        <del>{{goodsinfo.market_price}}</del>&nbsp;&nbsp;<span class="now-price">{{goodsinfo.sell_price}}</span>
                    </p>
                    <!-- watch -->
                    <p class="num">
                        <numberbox @getcount="getSelectedCount" :max="goodsinfo.stock_quantity"></numberbox>
                    </p>
                    <p>
                        <mt-button type="primary" size="small"></mt-button>
                        <mt-button type="danger" size="small" @click="addToShopCar"></mt-button>
                    </p>
                </div>
            </div>
        </div>
        <!--  -->
        <div class="mui-card">
            <div class="mui-card-header"></div>
            <div class="mui-card-content">
                <div class="mui-card-content-inner">
                    <p>{{goodsinfo.goods_no}}</p>
                    <p>{{goodsinfo.stock_quantity}}</p>
                    <p>{{goodsinfo.add_time | dataFormat}}</p>
                </div>
            </div>
            <div class="mui-card-footer">
                <mt-button type="primary" size="large" plain @click="goodsDesc(id)"></mt-button>
                <mt-button type="danger" size="large" plain @click="goodsComment(id)"></mt-button>
            </div>
        </div>

    </div>
</template>
<script type="text/javascript">
import swiper from '../subcomponents/swiper';
import numberbox from '../subcomponents/goodsinfo_num.vue';
import { Toast } from 'mint-ui';
     export default{
        name:'goodsinfo',
        data(){
            return{
                pageIndex:1,
                lunbolist:[],//
                id:this.$route.params.id,//id
                goodsinfo:{},//
                flag:false,//
                selectedCount:1//
            }
        },
        created(){
            this.getlunbotu();
            this.getGoodsInfo();//
        },
        
        methods:{
            getlunbotu(){
                this.$http.get('api/getthumimages/'+this.id).then((res)=>{
                    if(res.body.status==0){
                        res.body.message.forEach((item)=>{
                            item.img=item.src;
                        })
                        this.lunbolist=res.body.message;
                    }
                })
            },
            getGoodsInfo(){
                this.$http.get('api/goods/getinfo/'+this.id).then((res)=>{
                    if(res.body.status==0){
                        this.goodsinfo=res.body.message[0];
                    }
                })
            },
            goodsDesc(id){
                //,
                this.$router.push({name:'goodsdesc',params:{id}})
            },
            goodsComment(id){
                //,
                 this.$router.push({name:'goodscomment',params:{id}})
            },
            addToShopCar() {
              // 
              this.flag = !this.flag
              // { id:id, count: , price: selected: false  }
              //  store  car  
              var goodsinfo = {
                id: this.id,
                count: this.selectedCount,
                price: this.goodsinfo.sell_price,
                selected: true
              };
              //  store  mutations 
              this.$store.commit("addToCar", goodsinfo);
            },
            beforeEnter(el) {
              // beforeEnter 
              // 
              el.style.transform = "translate(0, 0)";
            },
            enter(el, done){             
              //  el.offsetWidth 
              el.offsetWidth
              // enter  
              //  
              console.log(this.$refs)
              //
              const ballPosition = this.$refs.ball.getBoundingClientRect();
              //
              const badgePosition=document.getElementById("badge").getBoundingClientRect();
              const xDist = badgePosition.left - ballPosition.left;
              const yDist = badgePosition.top - ballPosition.top;

              el.style.transform = `translate(${xDist}px, ${yDist}px)`;
              //el.style.transform = "translate(93px, 260px)"
              el.style.transition = 'all 1s ease'

              //  done  afterEnter done  afterEnter 
              done()
            },
            afterEnter(el){
              //  afterEnter
              this.flag = !this.flag
            },
            getSelectedCount(count){
                //
                this.selectedCount=count;
                console.log(''+count)
            }
        },
        components:{
            "swiper-box":swiper,
            numberbox
        }
     }
</script>
<style lang="less" scoped>
@r:50rem;
.goodsinfo-container {
  background-color: #eee;
  overflow: hidden;

  .now_price {
    color: red;
    font-size: 16/@r;
    font-weight: bold;
  }

  .mui-card-footer {
    display: block;
    button {
      margin: 15/@r 0;
    }
  }

  .ball {
    width: 15/@r;
    height: 15/@r;
    border-radius: 50%;
    background-color: red;
    position: absolute;
    z-index: 99;
    top: 360/@r;
    left: 146/@r;
  }
}
</style>


