<template>
    <div class="cmt-con">
        <h3></h3>
        <hr />
        <textarea placeholder="120" v-model="msg" name="" id="" maxlength="120"></textarea>
        <mt-button type="primary" size="large" @click="postComment"></mt-button>
        <div class="cmt-list">
            <div class="cmt-item" v-for="(item,index) in commentlist" :key="index">
                <div class="cmt-title">
                    {{index+1}}&nbsp;{{item.user_name}}&nbsp;&nbsp;{{item.add_time | dataFormat}}
                </div>
                <div class="cmt-body">
                    {{item.content=='undefined'?'':item.content}}
                </div>
            </div>
        </div>
        <mt-button type="danger" size="large" @click.native="getMorecomment" plain></mt-button>
    </div>
</template>
<script type="text/javascript">
import { Toast } from 'mint-ui'
     export default{
        name:'comment',
        data(){
            return{
                pageIndex:1,//
                commentlist:[],
                msg:''
            }
        },
        created(){
            this.getcomment();
        },
        methods:{
            getcomment(){
                this.$http.get('api/getcomments/'+this.id+'?pageindex='+this.pageIndex).then((res)=>{
                    console.log(res);
                    if(res.body.status==0){
                        //this.commentlist=res.body.message;
                        this.commentlist=this.commentlist.concat(res.body.message);
                    }else{
                        Toast("")
                    }
                })
            },
            getMorecomment(){
                this.pageIndex++;
                this.getcomment();
            },
            postComment() {
              // 
              if (this.msg.trim().length === 0) {
                return Toast("");
              }

              // 
              // 1 URL
              // 2  { content: this.msg }
              // 3   { emulateJSON:true }
              this.$http.post("api/postcomment/" + this.id, {
                  content: this.msg.trim()
                })
                .then(function(result) {
                  if (result.body.status === 0) {
                    // 1. 
                    var cmt = {
                      user_name: "",
                      add_time: Date.now(),
                      content: this.msg.trim()
                    };
                    this.commentlist.unshift(cmt);
                    this.msg = "";
                  }
                });
            }
        },
        props:['id']

     }
</script>
<style lang="less" scoped>
    @r:50rem;
    .cmt-con{
        h3{font-size: 16/@r;}
        textarea{
            font-size: 14/@r;
            height:85/@r;
            margin:10/@r 0;
        }
        .cmt-list{
            margin:6/@r 0;
            .cmt-item{
                font-size: 13/@r;
                .cmt-title{
                    line-height: 30/@r;
                    background-color: #ddd;

                }
                .cmt-body{
                    line-height: 35/@r;
                    text-indent: 2em;
                }
            }
        }
    }
</style>


