// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import MintUI from 'mint-ui' 
//mint-ui
//import { Tabbar, TabItem ,Button , Header , Swipe, SwipeItem,Lazyload } from 'mint-ui';
import 'mint-ui/lib/style.css'
//mui
import './lib/mui/css/mui.min.css'
import './lib/mui/css/icons-extra.css'
//vue-resource
import VueResource from 'vue-resource'

import moment from 'moment'

import Vuex from 'vuex'

//
var car=JSON.parse(localStorage.getItem("car"))|| [];

//vuex
Vue.use(Vuex);
var store=new Vuex.Store({
    state:{//this.$store.state.count
        //data
        //state this.$store.state.count;
        //
        //
        count:0,
        car:car//car
        //{id:id,count:count,price:price,selectd:true}
    },
    mutations:{//this.$store.commit('increment')
        //storemutations
        //state
        //mutationsthis.$store.commit('increment') increment:
        //mutationsthis.$emit('')
        //mutations1state2commit
        increment(state){
            state.count++;
        },
        addToCar(state,goodsinfo){
            //some
            var flag=false;
            state.car.some((item)=>{
                if(item.id==goodsinfo.id){
                    item.count += parseInt(goodsinfo.count);
                    flag=true;
                    return true;
                }
            })
            //flagfalsepushpush
            if(!flag){
                state.car.push(goodsinfo);
            }   

            //carcarlocalStorage
            localStorage.setItem('car',JSON.stringify(state.car))         
        },
        //
        undataGoodsInfo(state,goodsInfo){
            state.car.some((item)=>{
                if(item.id==goodsInfo.id){
                    item.count = parseInt(goodsInfo.count);
                    return true;
                }
            })
            //carcarlocalStorage
            localStorage.setItem('car',JSON.stringify(state.car))
        },
        removeFormcar(state,id){
            //idstore
            state.car.some((item,i)=>{
                if(item.id==id){
                   state.car.splice(i,1);
                   return true; 
                }
            })
            //carlocalStorage
            localStorage.setItem('car',JSON.stringify(state.car))
        },
        undataSelected(state,info){
            state.car.some((item,i)=>{
                if(item.id==info.id){
                   item.selected=info.selected
                   return true; 
                }
            })
            //
            localStorage.setItem('car',JSON.stringify(state.car))
        }
    },
    getters:{//this.$store.getters.optCount
        //gettersmutations  count
        // this.$store.getters.optCount
        optCount:function(state){
            return state.count;
        },
        getAllCount:function(state){
            var c=0;
            state.car.forEach(item=>{
                c+=item.count;
            })
            return c;
        },
        //
        getGoodscount(state){
            var o={};
            state.car.forEach(item=>{
                o[item.id]=item.count
            })
            return o;
        },
        getGoodsSelected(state){
            var j={};
            state.car.forEach(item=>{
                j[item.id]=item.selected
            })
            return j;
        },
        getGoodsCountAndAmount(state){
            var o={
                count:0,//
                amount:0//
            }
            state.car.forEach(item=>{
                if(item.selected){
                    o.count += item.count;
                    o.amount += item.count*item.price;
                }
            })
            return o;
        }
    }
})

 //veu-preview
import VuePreview from 'vue-preview'
//vue-preview
Vue.use(VuePreview)

// VueResource 
Vue.use(VueResource)

// mint-ui
// Vue.component(Tabbar.name, Tabbar);
// Vue.component(TabItem.name, TabItem);
// Vue.component(Button.name, Button);
// Vue.component(Header.name, Header);
// Vue.component(Swipe.name, Swipe);
// Vue.component(SwipeItem.name, SwipeItem);
// Vue.use(Lazyload);

Vue.use(MintUI)
Vue.config.productionTip = false


//
Vue.filter('dataFormat',function(dataStr,pattern="YYYY-MM-DD HH:mm:ss"){
    return moment(dataStr).format(pattern);
})

Vue.http.options.root = 'http://www.liulongbin.top:3005/';
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



